.navbar{
    height: 100px;
    border: 1px solid red;
     
    .wrapper{
        span{
            font-weight: bold;
        }
        
        border: 1px solid blue;
        max-width: 1366px;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;
      
        .social-icon{
            display: flex;
            gap: 20px;
            img{
                height: 18px;
                width: 18px;
            }
        }
    }
}